          <erd-radiobutton :value="0" v-model="findReplace.selectedOnly">{{
            $t("editor.all-groups")
          }}</erd-radiobutton>
        </template>
        <template v-if="findReplace.type === 2">
          <label class="w-100">{{ $t("xtream.streams") }}</label>
          <erd-radiobutton
            class="mt-1"
