            >{{ $t("editor.selected-streams-only") }}</erd-radiobutton
          >
          <erd-radiobutton :value="0" v-model="findReplace.selectedOnly">{{
            $t("editor.all-streams")
          }}</erd-radiobutton>
        </template>
      </div>
      <template v-slot:footer>
        <erd-button
          @click="findAndReplaceModal(false)"
